@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins:wght@400;600&display=swap");
:root {
  --font-primary: "Poppins", sans-serif;
  --font-secondary: "Open Sans", sans-serif;
}

:root {
  --background: hsl(257, 40%, 49%);
  --hover: hsl(300, 69%, 71%);
}

html {
  font-size: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url(../images/bg-mobile.svg) no-repeat, var(--background);
  background-size: 100%;
}
@media (min-width: 48em) {
  body {
    background: url(../images/bg-desktop.svg), var(--background);
  }
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 1.25rem;
}
@media (min-width: 48em) {
  .container {
    padding: 0 1.25rem;
  }
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  margin-bottom: 3.75rem;
}
.header__logo {
  width: 8.125rem;
}
@media (min-width: 48em) {
  .header__logo {
    width: 10.625rem;
  }
}

.main {
  display: grid;
  grid-template-columns: 100%;
  gap: 3.125rem;
}
@media (min-width: 48em) {
  .main {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 64em) {
  .main {
    grid-template-columns: 1fr 30.625rem;
  }
}
@media (min-width: 64em) {
  .main .content {
    margin-top: 3.125rem;
  }
}
.main .content__title {
  font-family: var(--font-primary);
  font-weight: 600;
  text-align: center;
  color: #fff;
  margin-bottom: 1.25rem;
}
@media (min-width: 48em) {
  .main .content__title {
    font-size: 1.75rem;
    max-width: 25rem;
    text-align: inherit;
  }
}
@media (min-width: 64em) {
  .main .content__title {
    font-size: 2.1875rem;
  }
}
.main .content__description {
  font-family: var(--font-secondary);
  text-align: center;
  color: #fff;
  margin-bottom: 1.25rem;
}
@media (min-width: 48em) {
  .main .content__description {
    text-align: inherit;
  }
}
.main .content__btn {
  font-family: var(--font-primary);
  background-color: #fff;
  box-shadow: 3px 4px 7px rgba(0, 0, 0, 0.3);
  color: var(--background);
  border: none;
  border-radius: 1.25rem;
  padding: 0.4375rem 4.375rem;
  display: block;
  margin: 0 auto;
  text-align: center;
  cursor: pointer;
  transition: all ease 0.5s;
}
.main .content__btn:hover {
  background-color: var(--hover);
  color: #fff;
}
@media (min-width: 48em) {
  .main .content__btn {
    margin: 0;
    padding: 0.625rem 3.125rem;
  }
}

.footer .social {
  margin-top: 3.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.9375rem;
}
@media (min-width: 48em) {
  .footer .social {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}
.footer .social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #fff;
  border-radius: 50%;
  padding: 0.4375rem;
  color: #fff;
  font-size: 1.25rem;
  transition: all ease 0.5s;
}
.footer .social__link:hover {
  border-color: var(--hover);
  color: var(--hover);
}